.App {
  background: url(/static/media/background-02.36742a39.jpg);
  height: 970px;
}

.body {
  margin: 0px;
  padding: 0px;
  font-family: sans-serif;
  background: white !important;
}


.header {
  background: rgba(0, 0, 0, 0) !important;
}

.header .title{
  float: left !important;
}

.header .menu {
  display: -webkit-flex;
  display: flex;
  border-bottom: 0px;
  -webkit-align-content: center;
          align-content: center;
  background: rgba(0, 0, 0, 0) !important;
}

.header .menu .nav {
  float: right !important;
}

.content {
  margin: auto;
  width: 80%;
  text-align: center;
  padding: 250px 0;
  background: white !important;
}

.content .title {
  font-size: 56px
}

.content .description {
  color: #9b9b9b;
  margin-top: 0px
}

.footer {
  background: #9b9b9b !important;
}

.bottom-layout {
  width: 100%;
  height: 20vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: row;
          flex-direction: row;
}
.bottom-layout svg {
  padding: 0 12px;
  height: 2rem;
  width: auto;
}

.bottom-layout a {
  text-decoration: none;
  color: #0aa00a;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.row-gutter {
    background: #fff !important;
}

.gutter-row {
    margin-bottom: 0px
}

.title {
    font-size: 48px;
    text-align: center;
}

.footer {
    height: 80px;
    margin: auto;
    background: #0aa00a !important;
    text-align: center;
    padding: 30px 0;
}

.content {
    margin: auto;
    width: 80%;
    height: 700px;
