#more {display: none;}

.content-detail {
    text-align: center;
    font-family: 'Franklin Gothic Medium';
    font-size: 24px;
}

.image-container {
    vertical-align: middle;
    display: inline-block;
    width: 50%;
    white-space: nowrap;
    overflow-x: auto;
    overflow-y: hidden;
  }

  #rounded-line-white {
